import React, { useState } from 'react';
import Navbar from './components/Navbar';
import Hero from './components/Hero';
import Services from './components/Services';
import Projects from './components/Projects';
import About from './components/About';
import Contact from './components/Contact';
import OrderForm from './components/OrderForm';
import Footer from './components/Footer';

function App() {
  const [activeSection, setActiveSection] = useState('home');

  return (
    <div className="min-h-screen bg-gray-50">
      <Navbar activeSection={activeSection} setActiveSection={setActiveSection} />
      
      <main>
        <section id="home" className={activeSection === 'home' ? 'block' : 'hidden'}>
          <Hero />
        </section>
        
        <section id="services" className={activeSection === 'services' ? 'block' : 'hidden'}>
          <Services />
        </section>
        
        <section id="projects" className={activeSection === 'projects' ? 'block' : 'hidden'}>
          <Projects />
        </section>
        
        <section id="about" className={activeSection === 'about' ? 'block' : 'hidden'}>
          <About />
        </section>
        
        <section id="contact" className={activeSection === 'contact' ? 'block' : 'hidden'}>
          <Contact />
        </section>
        
        <section id="order" className={activeSection === 'order' ? 'block' : 'hidden'}>
          <OrderForm />
        </section>
      </main>
      
      <Footer />
    </div>
  );
}

export default App;